.copyright {
  display: block;
  margin: auto;
  text-align: center;
}

.ad{
  margin: 20px auto;
  display: block;
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  font-family: sans-serif;
  text-decoration: none;
  color: #333;
  border: 2px solid #333;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
}

.ad span{
  position: relative;
  z-index: 2;
}

.ad:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #ff003b;
  transition: all .35s;
}

.ad:hover{
  color: #fff;
}

.ad:hover:after{
  width: 100%;
}