@import "../../../../Mixins";
@import "../../../../Variables";

.container {
  @include container;

}

.singleEx {
  @include expand-on-hover;
  @include shadow-lift;

  box-sizing: border-box;
  border-radius: 15px;

  display: flex;
  flex-basis: 45%;
  flex-direction: column;
  align-items: center;

  padding: 10px;

  img {
    width: 100%;
    object-fit: contain;
    border-radius: 15px 15px 0 0;
    margin-bottom: 15px;
  }

  @media (max-width: $s) {
    flex-basis: 100%;
    margin: 20px 0;
  }

  h3 {
    margin-top: auto;
  }
}