@import "../../../Variables";
@import "../../../Mixins";

.container {
  @include container;

  display: flex;
  flex-direction: column;

  button {
    transition: 0.2s;
    background-color: $menu-color;
    color: lighten($text-color, 20%);
    border: none;
    border-radius: 10px;
    font-size: 22px;
    width: 40%;
    height: 60px;
    margin: auto;
    cursor: pointer;
    position: relative;
    z-index: 0;

    @media (max-width: $m) {
      width: 50%;
    }

    @media (max-width: $s) {
     width: 75%;
    }

    &:hover {
      transition: 0.2s;
      background-color: darken($menu-color, 20%);
      color: $text-color;
    }
  }

  button:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
  }

  button:active {
    color: #000
  }

  button:active:after {
    background: transparent;
  }

  button:hover:before {
    opacity: 1;
  }

  button:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
  }

  @keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
  }

}