@import "../../../Variables";
@import "../../../Mixins";

.container {
  @include container;

  display: flex;
  flex-direction: column;
  align-items: center;

  .form {
    box-sizing: border-box;

    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 50%;

    @media (max-width: $s) {
      width: 100%;
    }

    @media (max-width: $m) {
      width: 75%;
    }


    input {
      box-sizing: border-box;
      font-family: 'Roboto', sans-serif;
      color: #333;
      font-size: 1.2rem;
      padding: 1.5rem 2rem;
      border-radius: 0.2rem;
      background-color: rgb(255, 255, 255);
      border: none;
      width: 100%;
      display: block;
      border-bottom: 0.3rem solid transparent;
      transition: all 0.3s;
    }

    label {
      font-family: 'Roboto', sans-serif;
      font-size: 1.2rem;
      margin-left: 2rem;
      margin-top: 0.7rem;
      display: block;
      transition: all 0.3s;
      transform: translateY(-0.4rem);
    }

    input:placeholder-shown + label {
      opacity: 0;
      visibility: hidden;
      transform: translateY(-2rem);
    }

    .letter {
      box-sizing: border-box;
      resize: none;
      width: 100%;

      padding: 1.5rem 2rem;
      border-radius: 0.2rem;
    }
  }
}