@import "../../../Mixins";
@import "../../../Variables";

.container {
  @include container;
}

.wrapper {

  padding: 30px 0;
  display: flex;
  align-items: center;


  .text {
    font-size: 30px;
    font-weight: bold;
  }

  .photo {
    margin-left: auto;
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 40px;
  }

  @media (max-width: 575px) {

    padding: 0 0 30px;
    flex-direction: column;

    .text {
      padding: 30px 0;
    }

    .photo {
      width: 300px;
      height: 300px;
      object-fit: cover;
      border-radius: 40px;
    }
  }
}