@import "../../Variables";
@import "../../Mixins";

.header {
  background-color: $menu-color;
  //display: flex;
  //align-items: center;

  .logo {
    font-weight: bold;
  }

  .container {
    @include container;
    display: flex;
  }

  .menu {
    color: $text-color;
    margin: 0 auto;

    &:hover {
      background-color: darken($menu-color,10%);
    }

    ul {
      margin: 0;
      padding: 0;
      display: flex;
      list-style-type: none;

      li {
        margin: 0 20px;
      }
    }
  }

  .selectLang {
    height: fit-content;
    //margin-right: 40px;
  }
}

