@import "Variables";

@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin horizontal-list {
  @include reset-list;

  li {
    display: inline-block;
    margin: {
      left: -2px;
      right: 2em;
    }
  }
}

@mixin container {

  box-sizing: border-box;
  margin: 10px auto;
  //width: 550px;
  //margin: 0 13px;

  // Extra Small devices (landscape phones, 320px and up)
  @media (min-width: $xs) and (max-width: $s) {

    width: 300px;
    padding: 0 10px;
  }

  // Small devices (landscape phones, 576px and up)
  @media (min-width: $s) and (max-width: $m) {

    width: 550px;
  padding: 0 13px;
  }

  // Medium devices (tablets, 768px and up)
  @media (min-width: $m) and (max-width: $l) {

    width: 768px - 26px;
    padding: 0 13px;
  }

  // Large devices (desktops, 992px and up)
  @media (min-width: $l) and (max-width: $xl) {


    width: 992px - 40px;
    padding: 0 20px;
  }

  // X-Large devices (large desktops, 1200px and up)
  @media (min-width: $xl) and (max-width: $xxl) {

    width: 1200px - 40px;
    padding: 0 20px;

  }

  // XX-Large devices (larger desktops, 1400px and up)
  @media (min-width: $xxl) {

    width: 1400px - 60px;
    padding: 0 30px;
  }
}

@mixin shadow-lift {
  box-shadow: 5px 5px 20px rgba(0,0,0,0.30) ;
}

@mixin expand-on-hover {
  transition: 0.2s;
  &:hover {
    transition: 0.2s;
    transform: scale(1.1);
  }
}