@import "../../../../Mixins";
@import "../../../../Variables";

.container {
  @include container;
}

.singleSkill {

  //self layout

  @include shadow-lift;
@include  expand-on-hover;

  position: relative;
  box-sizing: border-box;
  margin: 0 10px;
  flex-basis: 30%;
  border-radius: 15px;
  padding: 15px;

  //container layout
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    width: 150px;
    height: 150px;
    object-fit: contain;
  }

  @media (max-width: $m) {

    flex-basis: 29%;

    img {
      width: 100px;
      height: 100px;
      object-fit: contain;
    }
  }


  @media (max-width: $s) {
    flex-basis: 100%;
    margin: 20px 0;
  }
}